<template>
  <ModalPopup
    title="   "
    :visible.sync="modalVisible"
    width="1120px"
  >
    <div class="tbl-row">
      <table>
        <colgroup>
          <col class="col-row-title" />
          <col />
          <col class="col-row-title" />
          <col />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row"> </th>
            <td>{{ eduReportDetail.eduTxt }}</td>
            <th scope="row"> </th>
            <td>{{ eduReportDetail.eduAgency }}</td>
          </tr>
          <tr>
            <th scope="row"> </th>
            <td>
              {{ eduReportDetail.eduStartDt }} ~ {{ eduReportDetail.eduEndDt }}
            </td>
            <th scope="row"> </th>
            <td>{{ eduReportDetail.eduAddr }}</td>
          </tr>
          <tr>
            <th scope="row"></th>
            <td colspan="3">{{ eduReportDetail.eduAttendeeNm }}</td>
          </tr>
          <tr>
            <th scope="row">   </th>
            <td colspan="3">
              <div
                class="board-content"
                v-if="isHtmlText"
                v-html="eduReportDetail.eduContents"
              ></div>
              <div class="board-content" v-else>
                <Viewer :initialValue="eduReportDetail.eduContents" />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">    </th>
            <td colspan="3">
              <div
                class="board-content"
                v-if="isHtmlText"
                v-html="eduReportDetail.eduApplication"
              ></div>
              <div class="board-content" v-else>
                <Viewer :initialValue="eduReportDetail.eduApplication" />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">   ()</th>
            <td colspan="3">
              <div
                class="board-content"
                v-if="isHtmlText"
                v-html="eduReportDetail.eduOpinion"
              ></div>
              <div class="board-content" v-else>
                <Viewer :initialValue="eduReportDetail.eduOpinion" />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row"></th>
            <td>{{ eduReportDetail.regEmpNm }}</td>
            <th scope="row"></th>
            <td>{{ eduReportDetail.regDt }}</td>
          </tr>
          <tr>
            <th scope="row"></th>
            <td>{{ eduReportDetail.modEmpNm }}</td>
            <th scope="row"></th>
            <td>{{ eduReportDetail.modDt }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box" v-if="eduReportDetail.modifyYn === 'Y'">
      <button type="button" class="btn btn-primary" @click="this.openUpdatePop">
        
      </button>
    </div>
  </ModalPopup>
</template>

<script>
import ModalPopup from "@/components/ModalPopup";
import { Viewer } from "@toast-ui/vue-editor";

export default {
  name: "EduReportDetailPop",
  props: ["eduReportDetail", "openUpdatePop", "resetDetail", "isHtmlText"],
  components: {
    ModalPopup,
    Viewer,
  },
  data() {
    return {
      modalVisible: false,
      toUpdatePop: false,
    };
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val && !this.toUpdatePop) {
        this.resetDetail();
      }
    },
  },
  methods: {
    openPop() {
      this.modalVisible = true;
    },
    closePop() {
      this.modalVisible = false;
    },
    setToUpdatePop(isUpdatePop) {
      this.toUpdatePop = isUpdatePop;
    },
  },
};
</script>
